.movie__container {
  position: relative;
  width: 100%;
  height: 600px;
  overflow: hidden;
}

.movie__backdrop {
  width: 100%;
  height: 600px;
  /* object-fit:cover; */
  object-position: 0 35%;
 }  



.movie__title {
  font-size: 36px;
  color: white;
  position: absolute;
  top: 59%;
   left: 15%; 
  /* top: 71%; */
}

.movie__description {
  font-size: 27px;
  position: absolute;
  color: white;
  top: 84%;
  left: 5%;
  font-weight: lighter;

} 

.movie__btn{
  position: absolute;
  background-color: rgb(78, 5, 51);
  color: white;
  padding: 20px;
  border: 1px solid goldenrod;
  border-radius: 10px;
  top: 57%;
  left: 5%;
  font-size: 17px;
}

.watch__btn{
position: absolute;
background-color: rgb(78, 5, 51);
color: white;
padding: 20px;
border: 1px solid goldenrod;
border-radius: 10px;
top: 57%;
right: 5%;
font-size: 17px;
}

.movie__cast{
  position: absolute;
  top: 74%;
  right: 5%;
  font-size: 27px;
  color: white;
}

.movie__type{
  margin-top: 5px;
  position: absolute;
  top: 74%;
  left: 5%;
  font-size: 27px;
  color: white;
  font-weight: lighter;
}

@media (max-width: 768px) {
  .movie__title{
     left: 15%;
     top: 50%;
  }
   .movie__type{
    left: 15%;
    top: 68%;
   }

  .movie__cast{
    left: 15%;
    top: 75%;
  }
  .movie__btn{
    top: 40%;
    left: 5%;
  }
  .watch__btn{
    top: 40%;
    right: 5%;
  }

} 





  
