.posterImage{
    height: 600px;
}
.posterImage > img{
    margin: auto;
    display: block;
     width: 100%;
}

.posterImage__overlay{
    position: absolute;
    padding: 5rem;
    bottom: 0px;
    height: 70%;
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: flex-end;
    align-items: flex-start;
    background-image: linear-gradient(rgb(0,0,0,0), rgb(0,0,0,1));
    opacity: 1;
    transition: opacity .3s;

}

.posterImage__title{
    font-weight: 900;
    font-size: 4rem;
    margin-bottom: 0.4rem;
    text-align: left;
}

 .posterImage__type{
    margin-left: 3rem;

 }

.posterImage__description{
    font-style: italic;
    font-size: 1rem;
    margin-bottom: 0.25rem;
    display: flex;
    text-align: left;
    width: 50%;
}

.carousel{
    z-index: 1;
}

.slider-active .poster {
    display: none;
  }
  
  .slider {
    background-color: rgb(36, 29, 29);
    height: 100%;
    padding-top: 10px;
    position: fixed;
    right: 0;
    top: 0;
    width: 300px;
    z-index: 5;
  }

